import { apiHelper } from '../utils/helper.js'

export default {
  //  API
  signUp({ account, name, email, password }) {
    return apiHelper.post('/users', {
      account,
      name,
      email,
      password
    })
  },

  // API
  signIn({ account, password }) {
    return apiHelper.post('/signin', {
      account,
      password
    })
  }

}